import { Avatar, Box, Button, Grid, Stack, Tooltip, Typography } from "@mui/material";
import React, { useState } from "react";
import { useAppDispatch, useAppSelector } from "../../assets/hooks";
import { setPopup } from "../../store/Slices/popupSlice";
import { Event } from "../../types/types";
import Images from "../../assets";
import { addToShortList } from "../../services";
import {
  setShortlisted,
} from "../../store/Slices/shortlistedCoursesSlice";
import { months } from "../../assets/menu";
import { DestinationTypeEnum } from "../../assets/enums";
import FavoriteOutlinedIcon from '@mui/icons-material/FavoriteOutlined';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import { Info } from "@mui/icons-material";
import { singlePageStyles } from "src/components/styles/customStyles";
import { ScaleLoader } from "react-spinners";
import { formatCurrencyAmount } from "src/assets/currencyUtils";

const Courses = (props: any) => {

  const Cards = (data: any) => {
    const item = data.data;
    const fullToShort: Record<string, string> = Object.fromEntries(
      Object.entries(DestinationTypeEnum).map(([key, value]) => [value, key])
    );
    const userAuthStatus = useAppSelector((state) => state.userAuthStatus);
    const shortlisted = useAppSelector((state) => state.shortlistedcourses);
    const dispatch = useAppDispatch();
    const [loadingMap, setLoadingMap] = useState<{ [key: string]: boolean }>(
      {}
    );
    const CourseList = () => {
      const currentMonth = new Date().getMonth();

      const isApplySoon = item?.startDate?.some((course: any) => {
        const { deadlineMonth } = course;
        const monthsUntilDeadline = deadlineMonth - currentMonth;
        return monthsUntilDeadline >= 0 && monthsUntilDeadline <= 2;
      });
      return (
        <Stack spacing={2} sx={{ marginTop: "0px !important" }}>
          {isApplySoon && (
            <Box>
              <Button
                sx={{
                  color: "#C50000",
                  background: "#FFBEBE",
                  borderRadius: "0.8rem",
                  fontSize: "10px",
                  p: 0,
                  width: "4.5rem",
                  textTransform: "none",
                  "&:hover": {
                    color: "#C50000",
                    background: "#FFBEBE",
                  },
                }}
              >
                Apply Soon
              </Button>
            </Box>
          )}
        </Stack>
      );
    };
    const eventHandler = async (event: Event) => {
      switch (event.name) {
        case "applynow":
          if (
            userAuthStatus?.data?.isAuthorized &&
            userAuthStatus?.data?.isAuthorized
          ) {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "applynow",
                    data: {
                      _id: item.course?._id,
                      startDate: item.course?.startDate,
                      university: item.university,
                    },
                    dimensions: {
                      width: "350px",
                      show: "none"
                    },
                  },
                  type: "custom",
                },
              })
            );
          } else {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "login",
                    dimensions: {
                      width: "800px",
                      height: "70%",
                      show: "none"

                    },
                  },
                  type: "custom",
                },
              })
            );
          }
          break;
        case "shortlist":
          try {
            if (
              userAuthStatus?.data?.isAuthorized &&
              userAuthStatus?.data?.isAuthorized
            ) {
              setLoadingMap((prevLoadingMap) => ({
                ...prevLoadingMap,
                [event.data._id]: true,
              }));
              const values = {
                action: "push",
                courseId: event.data?._id,
              };
              const response = await addToShortList(values);
              if (response.data.success) {
                dispatch(setShortlisted(response.data.data))

                setLoadingMap((prevLoadingMap) => ({
                  ...prevLoadingMap,
                  [event.data._id]: false,
                }));
              } else {
                setLoadingMap((prevLoadingMap) => ({
                  ...prevLoadingMap,
                  [event.data._id]: false,
                }));
              }
            } else {
              dispatch(
                setPopup({
                  show: true,
                  data: {
                    container: {
                      name: "login",
                      dimensions: {
                        width: "800px",
                        height: "70%",
                        show: "none"

                      },
                    },
                    type: "custom",
                  },
                })
              );
            }
          } catch (error) {
            console.error("An error occurred while shortlisting:", error);
            setLoadingMap((prevLoadingMap) => ({
              ...prevLoadingMap,
              [event.data._id]: false,
            }));
          }
          break;
        case "delete":
          try {
            setLoadingMap((prevLoadingMap) => ({
              ...prevLoadingMap,
              [event.data._id]: true,
            }));
            const values = {
              action: "pull",
              courseId: event.data?._id,
            };
            const response = await addToShortList(values);
            if (response.data.success) {
              setLoadingMap((prevLoadingMap) => ({
                ...prevLoadingMap,
                [event.data._id]: false,
              }));
              dispatch(setShortlisted(response.data.data))

            }
          } catch (error) {
            setLoadingMap((prevLoadingMap) => ({
              ...prevLoadingMap,
              [event.data.course._id]: false,
            }));
            console.error("An error occurred while unshortlisting:", error);
          }
          break;
        default:
          break;
      }
    };
    return (
      <>
        <Box sx={{ background: "#EFF4F7", padding: "1rem", borderRadius: "15px", position: "relative" }} key={props.key}>
          {item.featured && <Typography sx={singlePageStyles.featured}>Featured</Typography>}
          <Grid container>
            <Grid item xs={12} md={5} sx={{ display: "flex", gap: "0.5rem" }}>
              <div
                style={{ display: "flex", gap: "10px", alignItems: "center", width: "360px", cursor: "pointer" }}
                onClick={(e) => {
                  // Prevent any default scroll behavior
                  e.preventDefault();
                  e.stopPropagation();
                  
                  // Store current scroll position before opening popup
                  const scrollPosition = window.scrollY;
                  console.log('=== COURSE CLICK DEBUG ===');
                  console.log('Current scroll position:', scrollPosition);
                  
                  // Only store scroll position if we're not at the top
                  if (scrollPosition > 0) {
                    localStorage.setItem('exploreScrollPosition', scrollPosition.toString());
                    console.log('Stored scroll position:', scrollPosition);
                  } else {
                    console.log('Already at top, no need to store scroll position');
                  }
                  
                  dispatch(
                    setPopup({
                      show: true,
                      data: {
                        container: {
                          name: "mainPopup",
                          data: item,
                          dimensions: {
                            width: "95%",
                            // height:"80%"
                          },
                        },
                        type: "custom",
                      },
                    })
                  );
                }}
              >
                <Box sx={{ display: "flex", flexDirection: "column" }} >

                  <Avatar
                    src={item?.university?.logoSrc}
                    alt="logo"
                    style={{
                      height: "2rem",
                      width: "2rem",
                      borderRadius: "50%",
                    }}
                  />
                </Box>
                <div style={{ display: "flex", flexDirection: "column" }}>
                  <Typography sx={{ fontSize: { xs: "14px", lg: "16px" } }}>{item?.name}</Typography>
                  <Typography sx={{ color: "#6A6A6A", mt: 0.5, fontSize: { xs: "13px", lg: "14px" } }}> {item?.university?.name}</Typography>
                  <div style={{ display: "flex", gap: "0.5rem" }}>
                    {item?.stemDetails?.stem ? (
                      <Button
                        sx={{
                          color: "#000",
                          background: "#72D4D1",
                          borderRadius: "1rem",
                          fontSize: "10px",
                          p: 0,
                          minWidth: "3rem",
                          width: "2.9rem",
                          mt: 0.5,
                          // left: "3rem",
                          "&:hover": {
                            color: "#000",
                            background: "#72D4D1",
                          },
                        }}
                      >
                        STEM
                      </Button>
                    ) : (
                      ""
                    )}
                    <CourseList />
                  </div>
                </div>

              </div>
              <Box sx={{ display: { xs: "block", md: "none" } }}>
                {loadingMap[item._id] ? (
                  <Box
                    component="img"
                    src={Images.standardLoader}
                    alt="standardLoader"
                    sx={{ width: "1rem", height: "1rem" }}
                  />
                ) : shortlisted.data.some(
                  (shortlistedCourse: any) =>
                    shortlistedCourse._id === item._id
                ) ? (
                  <FavoriteOutlinedIcon
                    sx={{ color: "#e35858", fontSize: "1.4rem" }}
                    onClick={() =>
                      eventHandler({
                        name: "delete",
                        data: item,
                      })
                    }
                  />
                ) : (
                  <FavoriteBorderIcon
                    sx={{ color: "#e35858", fontSize: "1.4rem" }}
                    onClick={() =>
                      eventHandler({
                        name: "shortlist",
                        data: item,
                      })
                    }
                  />
                )}
              </Box>
            </Grid>
            <Grid item xs={12} md={7} sx={{ padding: { xs: "1rem 3rem", md: 0 } }}>
              <Grid container spacing={2}>
                <Grid item xs={6} md={2} sx={{ display: { xs: "none", md: "block" } }}>
                  <Typography fontSize="13px" color="#6A6A6A">Country</Typography>
                  <Typography fontSize="14px" >
                    {fullToShort[item?.university?.location?.country] || "Unknown"}
                  </Typography>
                </Grid>
                <Grid item xs={6} md={2}>
                  <Typography fontSize="13px" color="#6A6A6A">Degree</Typography>
                  <Typography fontSize="14px">
                    {item.studyLevel}
                  </Typography>
                </Grid>
                <Grid item xs={6} md={2}>
                  <Typography fontSize="13px" color="#6A6A6A">Duration
                  </Typography>

                  {item?.duration?.length > 0 ? item?.duration?.map((value: any) => (
                    <Typography fontSize="14px" sx={{ display: "flex", alignItems: "center" }}> {value?.duration + " Months"}
                      <Tooltip
                        slotProps={{
                          tooltip: {
                            sx: {
                              color: "#fff",
                              boxShadow: 1,
                              backgroundColor: "#3B3F76",
                              "&.MuiTooltip-tooltip": {
                                marginTop: "-5px !important",
                              },
                            },
                          },
                        }}
                        title={value?.durationType}
                        placement="bottom"
                      >
                        <Info sx={{ fontSize: "small", cursor: "pointer", color: "#a7a1a1" }} />
                      </Tooltip>
                    </Typography>
                  )) : "N/A"}
                </Grid>
                <Grid item xs={6} md={2.5}>
                  <Typography fontSize="13px" color="#6A6A6A" sx={{ display: "flex", alignItems: "center" }}>Tuition Fee
                    <Tooltip
                      slotProps={{
                        tooltip: {
                          sx: {
                            color: "#fff",
                            backgroundColor: "#3B3F76",
                            boxShadow: 1,
                            "&.MuiTooltip-tooltip": {
                              marginTop: "-5px !important",
                            },
                          },
                        },
                      }}
                      title="Approximate"
                      placement="bottom"
                    >
                      <Info sx={{ fontSize: "small", cursor: "pointer", color: "#a7a1a1" }} />
                    </Tooltip>
                  </Typography>
                  <Typography
                    fontSize="0.85rem"
                    sx={{ display: "flex", alignItems: "center" }}
                  >
                    <Typography
                      sx={{
                        fontSize: "14px",
                        // margin: "2px 2px 0 0",
                        position: "relative",
                        cursor: "pointer",
                        "&:hover .cancel-icon": {
                          display: "block !important",
                        },
                      }}
                    >
                      {" "}
                      {item?.tuitionFee?.tuitionFee
                        ? item?.currency.symbol
                        : ""}
                      <span
                        className="cancel-icon"
                        style={{
                          display: "none",
                          color: "#000",
                          position: "absolute",
                          border: "1px solid #3B3F76",
                          width: "100px",
                          zIndex: 1,
                          background: "#fff",
                          textAlign: "center",
                          fontSize: "11px",

                        }}
                      >
                        Currency :{item?.currency.code}
                      </span>
                    </Typography>
                    {item?.tuitionFee?.tuitionFee || item?.tuitionFee?.tuitionFee > 0
                      ? formatCurrencyAmount(
                          Math.round(item?.tuitionFee?.tuitionFee * 100) / 100,
                          item?.currency?.code || 'INR'
                        )
                      : "N/A"}
                    <Typography
                      component="sub"
                      sx={{
                        fontSize: "9px",
                        marginLeft: "3px",
                        marginTop: "-3px",
                      }}
                    >
                      {item?.tuitionFee?.tuitionFee
                        ? item?.tuitionFee?.tuitionFeeType
                        : ""}
                    </Typography>
                  </Typography>
                </Grid>
                <Grid item xs={6} md={2.5}>
                  <Typography fontSize="13px" color="#6A6A6A">Course Starts</Typography>
                  <Typography fontSize="14px">
                    {item?.startDate
                      ?.map((item: any) => months[item.courseStartingMonth])
                      .join(" | ")}
                  </Typography>
                </Grid>
                <Grid item md={0.5} sx={{ display: { xs: "none", md: "block" },marginTop:2 }}>
                  {loadingMap[item._id] ? (
                    <Box
                      component="img"
                      src={Images.standardLoader}
                      alt="standardLoader"
                      sx={{ width: "1rem", height: "1rem"}}
                    />
                  ) : shortlisted.data.some(
                    (shortlistedCourse: any) =>
                      shortlistedCourse._id === item._id
                  ) ? (
                    <FavoriteOutlinedIcon
                      sx={{ color: "#e35858", fontSize: "1.4rem" }}
                      onClick={() =>
                        eventHandler({
                          name: "delete",
                          data: item,
                        })
                      }
                    />
                  ) : (
                    <FavoriteBorderIcon
                      sx={{ color: "#e35858", fontSize: "1.4rem",ml:1 }}
                      onClick={() =>
                        eventHandler({
                          name: "shortlist",
                          data: item,
                        })
                      }
                    />
                  )}
                </Grid>
              </Grid>

            </Grid>
          </Grid>
          {item.applicationDetails?.applicationFeeWaiver && <Typography sx={singlePageStyles.noCost}>No-Cost Apply</Typography>}
        </Box>
      </>
    )
  }

  const CourseCards = () => {
    return (
      <div>

        <Box sx={{
          minHeight: "600px", maxHeight: "650px",
          height: "100%",
          overflowY: "scroll",
        }}>
          <Grid
            container
            spacing={2}
            sx={{
              "&.MuiGrid-root": {
                marginTop: "0px !important"
              }
            }}
          >
            {props?.list?.length > 0 ? (
              props?.list?.map((course: any, i: number) => (
                <Grid item xs={12} >
                  <Cards data={course} key={course._id} />
                </Grid>
              ))
            ) :
              <Grid item xs={12} >
                <div style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "200px",
                  flexDirection: "column",
                }}>
                  <img
                    src={Images.noData}
                    alt="noData"
                    style={{ width: "70px", height: "70px" }}
                  />
                  <br />
                  No courses found
                </div>
              </Grid>
            }
          </Grid>
        </Box>
      </div>
    )
  }
  return (
    <Box sx={{ px: "1rem" }}>
      {props.loading ? (
        <Box sx={{ display: "flex", justifyContent: "center", alignItems: "center", minHeight: "400px" ,flexDirection:"column"}}>
          <ScaleLoader color="#3b3f76"/>
          <Typography>Please wait , while we fetch courses!!</Typography>
        </Box>
      ) : (
        <CourseCards />
      )}
    </Box>
  );
};

export default Courses;
