import { Avatar, Box, CircularProgress, Grid, Stack, Tooltip, Typography } from "@mui/material";
import { useAppDispatch } from "../../assets/hooks";
import { setPopup } from "../../store/Slices/popupSlice";
import { setWordCase } from "../../assets/library";
import { formatCurrencyAmount } from "src/assets/currencyUtils";
// import EastIcon from "@mui/icons-material/East";
import { useNavigate } from "react-router-dom";
import Images from "src/assets";
import { DestinationTypeEnum } from "src/assets/enums";
import { Info } from "@mui/icons-material";


const Universities = (props: any) => {
  const navigate = useNavigate();
  const dispatch = useAppDispatch();

  const Card = (data: any) => {
    const item = data.data;
    const fullToShort: Record<string, string> = Object.fromEntries(
      Object.entries(DestinationTypeEnum).map(([key, value]) => [value, key])
    );
    
    return (
      <div>
        <Box sx={{ background: "#EFF4F7", padding: "1rem", borderRadius: "10px", }}  >
          <Grid container>
            <Grid item xs={12} md={5}>
              <Box
                sx={{cursor:"pointer"}}
                onClick={(e) => {
                  // Prevent any default scroll behavior
                  e.preventDefault();
                  e.stopPropagation();
                  
                  // Store current scroll position before opening popup
                  const scrollPosition = window.scrollY;
                  console.log('Current scroll position (university):', scrollPosition);
                  
                  // Only store scroll position if we're not at the top
                  if (scrollPosition > 0) {
                    localStorage.setItem('exploreScrollPosition', scrollPosition.toString());
                    console.log('Stored scroll position (university):', scrollPosition);
                  } else {
                    console.log('Already at top, no need to store scroll position (university)');
                  }
                  
                  dispatch(
                    setPopup({
                      show: true,
                      data: {
                        container: {
                          name: "universityPopup",
                          data: item._id,
                          dimensions: {
                            width: "80%",
                            // height:"80%"
                          },
                        },
                        type: "custom",
                      },
                    })
                  );
                }}
              >
                <Box sx={{ display: "flex", gap: "10px", width: "100%", alignItems: "center" }}>
                  <Avatar
                    src={item?.logoSrc}
                    alt="logo"
                    style={{
                      width: "2rem",
                      height: "2rem",
                      marginRight: "5px",
                      borderRadius: "50%",
                    }}
                  />
                  <Stack sx={{}} spacing={1}>
                    <Typography
                      sx={{
                        fontSize: { xs: "14px", lg: "15px" },
                      }}
                    >
                      {item?.name}
                    </Typography>
                  </Stack>
                </Box>
              </Box>
            </Grid>
            <Grid item xs={12} md={7} sx={{padding:{xs:"1rem 3rem",md:0}}}>
              <Grid container spacing={3}>
                <Grid item xs={4} lg={2}>
                  <Typography fontSize="13px" color="#6A6A6A">Country</Typography>
                  <Typography fontSize="0.8rem" >
                    {fullToShort[item?.location?.country]}
                  </Typography>
                </Grid>
                <Grid item xs={4} lg={2.5}>
                  <Typography fontSize="13px" color="#6A6A6A">State</Typography>
                  <Typography fontSize="14px">
                    {item?.location?.state}
                  </Typography>
                </Grid>
                <Grid item xs={4} lg={2}>
                  <Typography fontSize="13px" color="#6A6A6A">Type</Typography>
                  <Typography fontSize="14px">
                    {setWordCase(item?.type)}
                  </Typography>
                </Grid>
                <Grid item xs={8} lg={3.5}>
                  <Typography fontSize="13px" color="#6A6A6A" sx={{display:"flex",alignItems:"center"}}>Cost of Living
                  <Tooltip
                    slotProps={{
                      tooltip: {
                        sx: {
                          color: "#fff",
                          // backgroundColor: "#3B3F76",
                          boxShadow: 1,
                          "&.MuiTooltip-tooltip": {
                            marginTop: "-5px !important",
                          },
                        },
                      },
                    }}
                    title="Approximate"
                    placement="bottom"
                  >
                    <Info sx={{fontSize:"small",cursor:"pointer",color:"#70A6B2"}}/>
                    </Tooltip>
                  </Typography>
                  <Typography fontSize="14px">
                    {item.cost?.find((item: any) => item.name === "Living Cost")
                      ? `${item.currency?.symbol || ""}${item.cost
                        .find((item: any) => item.name === "Living Cost")
                        ?.lowerLimit ? formatCurrencyAmount(item.cost.find((item: any) => item.name === "Living Cost")?.lowerLimit, item.currency?.code || 'INR') : '0'} - ${item.currency?.symbol || ""
                      }${formatCurrencyAmount(
                        item.cost.find((item: any) => item.name === "Living Cost")?.upperLimit,
                        item.currency?.code || 'INR'
                      )}`
                      : "Not available"}
                  </Typography>
                </Grid>
                <Grid item xs={4} lg={2}>
                  <Typography fontSize="13px" color="#6A6A6A">Programs </Typography>
                  <Typography fontSize="14px" sx={{display:"flex",alignItems:"center"}} >
                    {item?.courses}
                    {/* <img src={Images.arrow} alt="arrrow" style={{width:"16px",height:"16px",marginLeft:"10px",cursor:"pointer"}}  */}
                    {/* onClick={() => {
                      const subDisUrl = `activeTab=${JSON.stringify(
                        "courses"
                      )}&FilterData=${JSON.stringify([
                        {
                          type: "universityId",
                          data: [
                            { name: item?.name, value: item?._id },
                          ],
                        },
                      ])}&page=${JSON.stringify(1)}&search=${JSON.stringify(
                        ""
                      )}`
                      const subDisciUrl = window.btoa(subDisUrl);
                      navigate(`/explore?${subDisciUrl}`);
                    }}/> */}
                  </Typography>
                </Grid>

              </Grid>
            </Grid>
          </Grid>
        </Box>

      </div>
    );
  };
  const UniversityCards = () => {
    return (
      <div>

        <Box 
         sx={{
          minHeight: "600px", maxHeight: "650px",
          height: "100%",
          overflowY: "scroll",
        }}>
          <Grid
            container
            spacing={2}
            sx={{
              "&.MuiGrid-root":{
                marginTop:"0px !important"
             }
            }}
          >
            {props?.list?.length > 0 ? (
              props?.list?.map((course: any, i: number) => (
                <Grid item xs={12}>
                  <Card data={course} key={course._id} />
                </Grid>
              ))
            ) :
              <Grid item xs={12} >
                <div style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  height: "200px",
                  flexDirection: "column",
                }}>
                  <img
                    src={Images.noData}
                    alt="noData"
                    style={{ width: "70px", height: "70px" }}
                  />
                  <br />
                  No universities found
                </div>
              </Grid>

            }
          </Grid>
        </Box>
        
      </div>
    )
  }
  return (
    <Box sx={{ px: "1rem" }}>
      {props.loading ? (
        <Box sx={{ display: "flex", justifyContent: "center", alignItems: "center", minHeight: "400px" }}>
          <CircularProgress />
        </Box>
      ) : (
        <UniversityCards />
      )}
    </Box>
    
  );
};

export default Universities;
