import { Box, Button, debounce, Drawer, Grid, Pagination, Typography } from "@mui/material";
import React, { useCallback, useEffect, useRef, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";

import {
  Event,
  FilterInfo,
  FilterProps,
  FilterSelected,
  ListItem,
  exploreListInfo,
} from "../../types/types";
import Courses from "./courses";
import { getAlllistings } from "../../services";
import { useAppDispatch, useAppSelector } from "../../assets/hooks";
// import Filters from "../courses/filters";
import { filterOptions, quickOptions } from "../../assets/menu";
import Universities from "./universitites";
import TemporaryDrawer from "../courses/filterTransition";
import CloseIcon from "@mui/icons-material/Close";
import { setPopup } from "src/store/Slices/popupSlice";
import SortMenu from "../courses/sort";
import { FilterHandler, FiltersHandler, getExploreUrl, normalizeType } from "src/assets/library";
import Images from "src/assets";

const Explore = (props: FilterProps) => {
  const location = useLocation();
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const auth = localStorage.getItem("_auth")
  const searchString = useRef("");
  const page = useRef(0);
  const activeTab = useRef<string>("");
  const FilterData = useRef<FilterSelected[]>([]);
  // eslint-disable-next-line
  const [filterInfo, setFilterInfo] = useState<FilterInfo[]>([]);
  const [quickfilterInfo, setQuickFilterInfo] = useState<any[]>([]);
  const [list, setList] = useState([]);
  const currency = localStorage.getItem("currency");
  const preference = useAppSelector((state) => state.preferences);
  const [loading, setLoading] = useState(false);
  const itemsPerPage = 20;
  const currentPage = page.current;
  const [open, setopen] = useState(false);

  const listData = useRef<exploreListInfo>({
    type: "courses",
    page: 0,
    filterData: [],
    currency: "INR",
    search: "",
    totalItems: 0,
    totalPages: 1,
    appliedFilters: [],
  });

  console.log('list data',listData)
  const totalItems = listData.current.totalItems || 0;
  // console.log('total',totalItems)
  const startItem = totalItems > 0 ? (currentPage - 1) * itemsPerPage + 1 : 0;
  const endItem = Math.min(currentPage * itemsPerPage, totalItems);

  useEffect(() => {
    const encodedUrl = location.search.slice(1);
    const decodedUrl = window.atob(encodedUrl);
    const queryParams = new URLSearchParams(decodedUrl);
    let filterUrl = queryParams.get("FilterData");
    let activeUrl = queryParams.get("activeTab");
    let pageUrl = queryParams.get("page");
    let searchUrl = queryParams.get("search");
    page.current = pageUrl ? JSON.parse(pageUrl) : 1;
    FilterData.current =
      filterUrl !== undefined && filterUrl !== null
        ? JSON.parse(filterUrl)
        : [];
    activeTab.current = activeUrl ? JSON.parse(activeUrl) : "courses";
    searchString.current = searchUrl ? JSON.parse(searchUrl) : "";
    setFilterInfo(
      Object.values(filterOptions)[
      Object.keys(filterOptions).findIndex(
        (item) => item === activeTab.current
      )
      ]
    );
      setQuickFilterInfo(
      Object.values(quickOptions)[
      Object.keys(quickOptions).findIndex(
        (item) => item === activeTab.current
      )
      ]
    );
    listData.current.appliedFilters = FilterData.current;
    listData.current.type = activeTab.current;
    listData.current.search = searchString.current;

    let history = localStorage.getItem(activeTab.current + "ListInfo");
    listData.current.totalItems = history
      ? JSON.parse(history).totalItems
      : undefined;
    listData.current.totalPages = history
      ? JSON.parse(history).totalPages
      : undefined;
      debouncedFetchList(page.current, listData.current);
    // fetchList(page.current, listData.current);

    // Restore scroll position if it was stored
    const savedScrollPosition = localStorage.getItem('exploreScrollPosition');
    if (savedScrollPosition) {
      console.log('Restoring scroll position on page load:', parseInt(savedScrollPosition));
      setTimeout(() => {
        window.scrollTo(0, parseInt(savedScrollPosition));
      }, 100);
    }

    // eslint-disable-next-line
  }, [location.pathname]); // Use pathname instead of entire location object to prevent infinite loops

  // Cleanup stored scroll position when component unmounts
  useEffect(() => {
    return () => {
      // Don't remove scroll position on unmount, keep it for when user comes back
      // localStorage.removeItem('exploreScrollPosition');
    };
  }, []);

  // Add scroll event listener to store scroll position
  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY;
      if (scrollPosition > 0) {
        localStorage.setItem('exploreScrollPosition', scrollPosition.toString());
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const tabs = [
    {
      label: "Programs",
      value: "courses",
      Component: <Courses list={list} loading={loading}></Courses>,
    },
    {
      label: "Universities",
      value: "universities",
      Component: <Universities list={list} loading={loading}></Universities>,
    },
  ];

  const eventHandler = (event: Event) => {
    let url = '';

    switch (event.name) {
      case "tabSelected":
        let exploreHistory = localStorage.getItem(
          event.data.value + "ListInfo"
        );
        url = `activeTab=${JSON.stringify(
          event.data.value
        )}&FilterData=${JSON.stringify(
          exploreHistory ? JSON.parse(exploreHistory).appliedFilters : []
        )}&page=${JSON.stringify(1)}&search=${JSON.stringify("")}`;
        navigate(`/explore?${window.btoa(url)}`);
        break;

      case "filterUpdated":
        url = `activeTab=${JSON.stringify(
          activeTab.current
        )}&FilterData=${encodeURIComponent(JSON.stringify(event.data))}&page=${JSON.stringify(
          1
        )}&search=${JSON.stringify("")}`;
        navigate(`/explore?${window.btoa(url)}`);

        break;

      case "nextPage":
        url = `activeTab=${JSON.stringify(
          activeTab.current
        )}&FilterData=${JSON.stringify(
          FilterData.current
        )}&page=${JSON.stringify(page.current + 1)}&search=${JSON.stringify(
          searchString.current
        )}`;
        navigate(`/explore?${window.btoa(url)}`);

        break;

      case "pageChange":
        if (event.data > 2) {
          if (auth) {
            let url = `activeTab=${JSON.stringify(
              activeTab.current
            )}&FilterData=${encodeURIComponent(JSON.stringify(
              FilterData.current
            ))}&page=${JSON.stringify(event.data)}&search=${JSON.stringify(
              searchString.current
            )}`;
            navigate(`/explore?${window.btoa(url)}`);
            window.scrollTo(0, 0);
          } else {
            dispatch(
              setPopup({
                show: true,
                data: {
                  container: {
                    name: "login",
                    dimensions: {
                      width: "800px",
                      height: "80%",
                      show: "none",
                    },
                  },
                  type: "custom",
                },
              })
            );
          }
        } else {
          let url = `activeTab=${JSON.stringify(
            activeTab.current
          )}&FilterData=${encodeURIComponent(JSON.stringify(
            FilterData.current
          ))}&page=${JSON.stringify(event.data)}&search=${JSON.stringify(
            searchString.current
          )}`;
          navigate(`/explore?${window.btoa(url)}`);
          window.scrollTo(0, 0);
        }
        break;

      case "search":
        url = `activeTab=${JSON.stringify(
          activeTab.current
        )}&FilterData=${JSON.stringify(
          FilterData.current
        )}&page=${JSON.stringify(1)}&search=${JSON.stringify(event.data)}`;
        navigate(`/explore?${window.btoa(url)}`);

        break;

      case "previousPage":
        url = `activeTab=${JSON.stringify(
          activeTab.current
        )}&FilterData=${JSON.stringify(
          FilterData.current
        )}&page=${JSON.stringify(page.current - 1)}&search=${JSON.stringify(
          searchString.current
        )}`;
        navigate(`/explore?${window.btoa(url)}`);
        break;
    }
  };

  const isRequestInProgress = useRef(false); 
  const queuedUpdate = useRef(false); 

  const fetchList = useCallback(
    async (requiredPage: number, listInfo: exploreListInfo) => {
      if (isRequestInProgress.current) {
        queuedUpdate.current = true; 
        return;
      }

      isRequestInProgress.current = true; 

      if (requiredPage === 1) {
        listInfo.totalPages = 1;
      }

      if (listInfo.totalPages && requiredPage <= listInfo.totalPages) {
        let filters = listInfo?.appliedFilters?.map((filter: any) => ({
          ...filter,
          data: filter?.data.map((item: any) => item?.value),
        }));
        setLoading(true);
        try {
          const response = await getAlllistings(listInfo.type, {
            page: requiredPage,
            currency: preference.data?.currency
              ? preference.data.currency
              : currency
              ? currency
              : "INR",
            filterData: listInfo.search
              ? [...filters, { type: "name", data: [listInfo.search] }]
              : filters,
          });
          if (response.success) {
            listInfo.totalItems = response.data.totalItems;
            listInfo.totalPages = response.data.totalPages;
            localStorage.setItem(
              listInfo.type + "ListInfo",
              JSON.stringify(listInfo)
            );
            setList(response.data.list);
          }
        } catch (err) {
          console.error(err);
        } finally {
          setLoading(false);
          isRequestInProgress.current = false; 
          if (queuedUpdate.current) {
            queuedUpdate.current = false;
            fetchList(page.current, listData.current);
          }
        }
      }
    },
    [preference, currency]
  );
  
// eslint-disable-next-line
  const debouncedFetchList = useCallback(
    debounce((requiredPage, listInfo) => {
      fetchList(requiredPage, listInfo);
    }, 200), 
    [fetchList]
  );


  return (
    <Box sx={{
      display: "flex",
      justifyContent: "center", alignItems: "center",
      width: "100%", marginTop: "4rem"
    }}>
      <div style={{
        maxWidth: "1440px",
        width: "100%",
        marginBottom: "4rem",
        // overflowY: "scroll"
      }}>
        <Grid container spacing={1}>
          <Grid item lg={2.5} sx={{ display: { xs: "none", lg: "block" }, paddingLeft: "16px !important" }}>
            <Box sx={{
              boxShadow: "0px 4px 16px 0px #00000040",
              width: "90%",
              mt: 2,
              borderRadius: "1rem",
              height: "calc(100vh - 200px)",
              overflowY: "auto",
              overflowX: "hidden",
              // Hide scrollbar for all browsers
              '::-webkit-scrollbar': { display: 'none' },
              scrollbarWidth: 'none',
              msOverflowStyle: 'none',
            }}>
              <TemporaryDrawer
                appliedFilters={FilterData.current}
                setAppliedFilter={(data: FilterSelected[]) => {
                  eventHandler({ name: "filterUpdated", data: data });
                }}
              // options={filterInfo}
              />
            </Box>
          </Grid>

          <Grid item xs={12} lg={9.5} sx={{
            height: "calc(100vh - 200px)",
            overflowY: "auto",
            overflowX: "hidden",
            // Hide scrollbar for all browsers
            '::-webkit-scrollbar': { display: 'none' },
            scrollbarWidth: 'none',
            msOverflowStyle: 'none',
           
          }}>
            <Grid container sx={{ display: "flex", alignItems: "center" }}>
              <Grid item xs={9} sm={10} lg={12} sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                {startItem > 0 ?
                  <Typography sx={{ display: "flex", gap: "0.5rem", pl: 2 }}>
                    <span style={{ color: "#495052", fontWeight: "600" }}>
                      {startItem}-{endItem}
                    </span>
                    out of
                    <span style={{ color: "#495052", fontWeight: "600" }}>
                      {totalItems}
                    </span>
                    results
                  </Typography>
                  : <Typography sx={{ display: "flex", gap: "0.5rem", pl: 2 }}>
                    <span style={{ color: "#495052", fontWeight: "600" }}>
                      {totalItems}
                    </span>
                    results
                  </Typography>}
                {
                  activeTab.current === "courses" &&
                  <SortMenu setList={setList} list={list} />
                }
              </Grid>
              <Grid item xs={3} sm={2} sx={{ display: { lg: "none" } }}>
                <Button
                  onClick={() => setopen(true)}
                  sx={{
                    display: "flex",
                    gap: "6px",
                    color: "#000",
                    fontWeight: "500",
                    textTransform: "none",
                    mb: 1
                  }}
                >
                  <img src={Images.filterIcon} alt="icon" style={{ width: "20px" }} /> Filters
                </Button>
              </Grid>

            </Grid>
            <Box
              sx={{
                display: "flex", 
                flexWrap: "nowrap", 
                alignItems: "center", 
                overflowX: "auto",
                paddingLeft: "1rem",
                scrollbarWidth:"none",
              mb:2
              }}
            >
              {quickfilterInfo?.map((item, i) => (
                <FilterContainer
                  key={i}
                  data={item}
                  appliedFilter={
                    FilterData.current?.find((item2) => item2.type === item.type) || {
                      type: item.type,
                      data: [],
                    }
                  }
                  onSelection={FilterHandler}
                  normalizeType={normalizeType}
                  appliedFilters={FilterData.current}
                />
              ))}
            </Box>

            <Box>
              {tabs.find((item) => item.value === activeTab.current)?.Component}
            </Box>
            <Box sx={{ display: "flex", justifyContent: "center", gap: "20px", my: 2 }}>
              {startItem > 0 && <Pagination
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  mt: 1,
                  "& .MuiPaginationItem-root.Mui-selected": {
                    background: "#F48438 !important",
                    color: "#fff",
                    borderRadius: "5px",
                    height: "28px",
                    minWidth: "28px !important",
                    p: "0 !important"
                  },
                }}
                count={listData.current.totalPages}
                page={page.current}
                onChange={(event, value) =>
                  eventHandler({ name: "pageChange", data: value })
                }
                showFirstButton
                showLastButton
              />}
            </Box>
          </Grid>

          {open ? (
            ["right"].map((anchor: any) => (
              <React.Fragment key={anchor}>
                <Drawer anchor={anchor} open={open} onClose={() => setopen(!open)} sx={{
                  "& .MuiPaper-root": {
                    width: { xs: "60%", sm: "40%", md: "30%", }
                  }, display: { xs: "block", lg: "none" }
                }}>
                  <Box>
                    <CloseIcon sx={{ float: "right", m: 1, cursor: "pointer" }} onClick={() => setopen(!open)} />
                  </Box>
                  <TemporaryDrawer
                    appliedFilters={FilterData.current}
                    setAppliedFilter={(data: FilterSelected[]) => {
                      eventHandler({ name: "filterUpdated", data: data });
                    }}
                  // options={filterInfo}
                  />
                </Drawer>
              </React.Fragment>
            ))
          )
            : null}

        </Grid>
      </div>
    </Box>
  );
};


const FilterContainer = (props: {
  data: FilterInfo;
  initialValue?: FilterSelected | undefined;
  onSelection: any;
  normalizeType: any;
  appliedFilter: any;
  appliedFilters: FilterSelected[] | undefined;
}) => {
  return (
    <>
      <DefaultContainer
        appliedFilter={props?.appliedFilter}
        data={props?.data}
        initialValue={
          props?.initialValue
            ? props?.initialValue
            : { type: props?.data?.type, data: [] }
        }
        onSelection={props?.onSelection}
        normalizeType={props?.normalizeType}
      />
    </>
  );
};


const DefaultContainer = (props: {
  data: FilterInfo;
  initialValue: FilterSelected | undefined;
  onSelection: any;
  normalizeType: any;
  appliedFilter: FilterSelected;
}) => {
  const navigate = useNavigate();
  const location = useLocation();
  const onSelection = (val: ListItem) => {
    console.log('Hello',val)
    let filterData = FilterHandler({
      data: val,
      filterInfo: props.data,
      filter: props.appliedFilter,
    });
    const encodedUrl = location.search.slice(1);
    let decodedUrl = "";
    try {
      decodedUrl = window.atob(encodedUrl);
    } catch (error) {
      console.error("Error decoding URL", error);
      decodedUrl = "";
    }

    const urlParams = new URLSearchParams(decodedUrl);
    let FilterUrl = urlParams.get("FilterData");
    let allFilters = FiltersHandler(
      filterData,
      FilterUrl ? JSON.parse(FilterUrl) : []
    );
    navigate(getExploreUrl("filterData", allFilters));
  };

  return (
    <>
      {props?.data?.options?.map((item, i) => (
        <Button
        key={i}
          sx={{
            border: props.appliedFilter?.data.find(
              (data) =>
                data.value === item.value
            )
              ? "1px solid var(--3-primary-teal, #70A6B2)" : "1px solid #70A6B2",
            backgroundColor: props.appliedFilter?.data.find(
              (data) =>
                data.value === item.value
            )
              ? "#70A6B2"
              : "transparent",
            color:props.appliedFilter?.data.find(
              (data) =>
                data.value === item.value
            )? "#ffff": "#495052",
            textTransform: "none",
            marginRight: "0.5rem",
            fontSize: "13px",
            borderRadius: "8px",
            "&:hover": {
              background: "#70A6B2",
              color:"#ffff"
            }, width: "auto",
            flexShrink:0
          }}
          onClick={(e) => onSelection(item)}
        >
          {props.normalizeType(props.data.label)}
        </Button>
      ))}
    </>
  );
};

export default Explore;
